.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

html {
  overflow-y: scroll;
}
a {
  text-decoration: none !important;
  color: #519cdd;
}
h1 {
  text-transform: uppercase;
}
.row {
  display: flex;
  flex-direction: row;
  .col-md-1 {
    width: 8.33%;
  }
  .col-md-2 {
    width: 16.67%;
  }
  .col-md-3 {
    width: 25%;
  }
  .col-md-4 {
    width: 33.33%;
  }
  .col-md-5 {
    width: 41.67%;
  }
  .col-md-6 {
    width: 50%;
  }
  .col-md-7 {
    width: 58.33%;
  }
  .col-md-8 {
    width: 66.67%;
  }
  .col-md-9 {
    width: 75%;
  }
  .col-md-10 {
    width: 83.33%;
  }
  .col-md-11 {
    width: 91.67%;
  }
  .col-md-12 {
    width: 100%;
  }
}
.btn {
  padding: 10px 12px;
  &.btn-primary {
    background-color: #519cdd;
    color: #fff;
    border: 1px solid transparent;
  }
  &:hover {
    cursor: pointer;
  }
}
.mar-0 {
  margin: 0;
}
.pad-0 {
  padding: 0 !important;
}
.cms-container {
  height: 100%;
  padding: 0;
  /* margin: 0; */
}
.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.row {
  width: 100%;
  margin: 0;
}
.left-menu-col,
.right-menu-col {
  width: 40%;
}

.col-md-1,
.cms-col {
  height: 100%;
  padding: 0 8px;
  box-sizing: border-box;
}
.img-container {
  margin: 0 auto;
  width: 50px;
  height: 50px;
  //   background: #dfbaba;
  display: block;
  overflow: hidden;
  border-radius: 50%;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.body-container {
  display: grid;
}

.btn {
  &.hire-me {
    margin: 10px auto;
    line-height: 1.5;
    border: 1px solid #519cdd;
    background-color: transparent;
    color: #428bca;
    display: flow-root;
    width: fit-content;
    min-width: 100px;
    padding: 6px 10px;
    text-align: center;
    &:hover {
      background: #77baf5;
      border: 1px solid #428bca;
      color: #fff;
    }
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
